<template>
<div id="users">
    <!--  -->
    <el-card :body-style="{ padding: '0px' }">
        <div slot="header">
            <span></span>
            <!--  -->
            <el-button @click="btnStatus=true;dialogVisible=true;dialogTitle=''" type="primary" size="mini" icon="el-icon-plus"></el-button>
        </div>
        <el-table :data="tableData">
            <!--  -->
            <el-table-column prop="classname" label=""></el-table-column>
            <!--  -->
            <el-table-column prop="status" label="">
                <template slot-scope="{row}">
                    <el-tag v-show="row.status==0" type="success"></el-tag>
                    <el-tag v-show="row.status==1" type="info"></el-tag>
                    
                </template>
            </el-table-column>
            <!--  -->
            <el-table-column prop="" label="">
                <div slot-scope="{row}">
                    <el-button-group>
                        <!--  -->
                        <el-button type="default" @click="usersEdit(row);btnStatus=false" round size="mini"></el-button>
                        <el-button type="default" @click="usersDelete(row.id)" round size="mini"></el-button>
                    </el-button-group>
                </div>
            </el-table-column>
        </el-table>
        <!-- element-ui -->
        <div style="margin-top:20px">
            <el-pagination :current-page.sync="currentpage" :page-size="pagesize" @current-change="getUsersList" :total="total" background layout="prev,pager,next">
            </el-pagination>
        </div>
    </el-card>
    <!-- dialog -->
    <el-dialog width="400px" :title="dialogTitle" :visible.sync="dialogVisible" @close="dialogClose">
        <el-form label-position="left" :model="formData" :rules="rules" ref="formData" label-width="60px">
            <!--  -->
            <el-form-item label="" prop="classname">
                <el-input v-model="formData.classname"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="" prop="status">
                <el-tag size="mini"></el-tag>
                <el-switch v-model="formData.status" :active-value="0" :inactive-value="1"></el-switch>
                <el-tag size="mini"></el-tag>
                <!-- <el-radio v-model="formData.status" label="0"></el-radio>
                <el-radio v-model="formData.status" label="1"></el-radio> -->
            </el-form-item>
        </el-form>
        <!--  -->
        <div slot="footer">
            <el-button type="danger" @click="dialogVisible=false"> </el-button>
            <el-button type="primary" @click="menusAdd" v-show="btnStatus"> (add)</el-button>

            <!--  -->
            <el-button type="primary" @click="menusEdit" v-show="!btnStatus"> (edit)</el-button>
        </div>
    </el-dialog>
</div>
</template>

<script>
export default {
    data() {
        // 
        var classnameRegx = (rule, value, callback) => {
            this.$http.get(`/classes/isexist?fieldname=classname&fieldvalue=${value}`).then((result) => {
                if (result.data.error == 0 || this.oldclassname == value) {
                    callback()
                } else {
                    callback(new Error(','));
                }
            })
        }
        return {
            rules: {
                // 
                classname: [{
                        validator: (rules, value, callback) => {
                            switch (true) {
                                case value.trim() === '':
                                    callback(new Error(''));
                                    break;
                                case !/^[0-9]{3,9}$/.test(value):
                                    callback(new Error('39'));
                                    break;
                                default:
                                    callback();
                            }
                        }
                    },
                    {
                        validator: classnameRegx,
                        trigger: 'blur'
                    },
                ],
                // 
                status: [{
                    validator: (rules, value, callback) => {
                        switch (true) {
                            // case value.trim() === '':
                            //         callback(new Error(''));
                            //         break;
                            // case !/^(0|1)$/.test(value):
                            //     callback(new Error('01,'));
                            //     break;
                            default: callback();
                        }
                    }
                }],
            },
            // 
            btnStatus: true,
            tableData: [],
            total: 0,
            pagesize: 4,
            currentpage: 1,
            dialogTitle: '',
            dialogVisible: false, //dialog
            oldclassname: '',
            formData: {
                classname: "",
                status: "",
            },
        }
    },
    created() {
        this.getUsersList()
    },
    methods: {
        // 
        getUsersList() {
            this.$http.get(`/classes/list?pagesize=${this.pagesize}&currentpage=${this.currentpage}`).then((result) => {
                this.total = result.data.total
                this.tableData = result.data.data
            })
        },
        dialogClose() {
            this.$refs.formData.resetFields() //
        },
        // 
        usersEdit(row) {
            this.dialogVisible = true //dialog
            this.dialogTitle = '' //dialog
            // 
            this.formData.classname = row.classname
            this.formData.status = row.status
            
            // id
            this.$set(this.formData, 'id', row.id)
            this.oldclassname = row.classname
        },
        //
        menusEdit() {
            this.$refs['formData'].validate((pass) => {
                if (pass) {
                    this.$http.post('/classes/edit', this.$qs.stringify(this.formData)).then((result) => {
                        // if(result.error==0){
                        //     this.$http.get('/classes/list').then((result)=>{
                        //         if(result.error==0){
                        //             this.formData = result.data.data
                        //         }
                        //     })
                        // }
                        this.dialogVisible = false //dialog
                        this.getUsersList() //
                    })
                } else {
                    this.dialogVisible = true
                }
            })

        },
        // 
        menusAdd() {
            this.$refs.formData.validate((pass) => {
                if (!pass) {
                    return;
                }
                this.$http
                    .post("/classes/add", this.$qs.stringify(this.formData))
                    .then(result => {
                        // this.getMenusList();
                        this.getUsersList()
                        this.dialogVisible = false; //dialog
                        // this.$root.$emit('getMenusList')
                    });
            })
        },
        // 
        usersDelete(id) {
            this.$confirm('?', '', {
                type: 'warning'
            }).then(() => {
                this.$http.get(`/classes/delete?id=${id}`).then((result) => {
                    this.getUsersList() //
                })
            }).catch(() => {
                console.log('no')
            })
        },
    }
}
</script>

<style>
.el-card {
    padding: 20px;
}
.el-button--primary {
    float: right;
}
</style>



