<template>
<div id="regist">
    <el-card class="card">
        <div slot="header">
            <!--  -->            
            <img src="/static/logo.png">
            <span></span>
        </div>

        <el-form ref="formLogin" :model="formLoginData" :rules="rules" label-width="80px">
            <!-- input -->
            <el-form-item label="" prop="username">
                <el-input v-model="formLoginData.username"></el-input>
            </el-form-item>
            <!-- input -->
            <el-form-item label="" prop="password">
                <el-input type="password" v-model="formLoginData.password"></el-input>
            </el-form-item>
            <!-- input -->
            <el-form-item label="" prop="password2">
                <el-input type="password" v-model="formLoginData.password2"></el-input>
            </el-form-item>
            <!-- input -->
            <el-form-item label="" prop="email">
                <el-input v-model="formLoginData.email"></el-input>
            </el-form-item>
            <!-- input -->
            <el-form-item label="" prop="phone">
                <el-input v-model="formLoginData.phone"></el-input>
            </el-form-item>
            <el-form-item>
                <!--  -->
                <el-button type="primary" @click="regist"></el-button>
                <router-link to="/login">
                    <el-button type="success" class="success"></el-button>
                </router-link>
            </el-form-item>
        </el-form>

    </el-card>
</div>
</template>

<script>
import qs from 'qs'

export default {
    data() {
        // 
        let validUsername = (rule, value, callback) => {
            this.$http.get(`/users/isexist?fieldname=username&fieldvalue=${value}`).then((result) => {
                if (result.data.error == 0 || this.oldusername == value) {
                    callback()
                } else {
                    callback(new Error(','));
                }
            })
        }
        // 
        let validPassword = (rule, value, callback) => {
            let regx = /^.{6,15}$/
            if (!regx.test(value)) {
                callback(new Error('615'));
            } else {
                callback();
            }
        }
        // 
        let validPassword2 = (rule, value, callback) => {
            let regx = /^.{6,15}$/
            if (value !== this.formLoginData.password) {
                callback(new Error(','));
            } else {
                callback();
            }
        }
        // 
        let validEmail = (rule, value, callback) => {
            let regx = /^\w+@\w+(\.(com|cn|net|org|edu)){1,2}$/g
            if (!regx.test(value)) {
                callback(new Error(','));
            } else {
                callback();
            }
        }
        // 
        let validPhone = (rule, value, callback) => {
            let regx = /^((13|18)(\d{9}))$|^(14[57]\d{8})$|^(17[07]\d{8})$|(^15[0-35-9]\d{8}$)/
            if (!regx.test(value)) {
                callback(new Error(','));
            } else {
                callback();
            }
        }
        return {
            // 
            formLoginData: {
                username: "",
                password: "",
                password2: "",
                email: "",
                phone: ""
            },
            // 
            rules: {
                username: [{
                    // 
                        validator: (rules, value, callback) => {
                            switch (true) {
                                case value.trim() === '':
                                    callback(new Error(''));
                                    break;
                                case !/^[a-zA-Z]\w{3,14}$/.test(value):
                                    callback(new Error('415'));
                                    break;
                                default:
                                    callback();
                            }
                        }
                    },
                    {
                        validator: validUsername,
                        trigger: "blur"     //
                    }
                ],
                password: [
                    {
                        // 
                        validator: (rules, value, callback) => {
                            switch (true) {
                                case value.trim() === '':
                                    callback(new Error(''));
                                    break;
                                default:
                                    callback();
                            }
                        }
                    },
                    {
                    validator: validPassword,
                    trigger: "blur"     //
                }],
                password2: [
                    {
                        // 
                        validator: (rules, value, callback) => {
                            switch (true) {
                                case value.trim() === '':
                                    callback(new Error(''));
                                    break;
                                default:
                                    callback();
                            }
                        }
                    },
                    {
                    validator: validPassword2,
                    trigger: "change"
                }],
                email: [
                    {
                        // 
                        validator: (rules, value, callback) => {
                            switch (true) {
                                case value.trim() === '':
                                    callback(new Error(''));
                                    break;
                                default:
                                    callback();
                            }
                        }
                    },
                    {
                    validator: validEmail,
                    trigger: "change"
                }],
                phone: [
                    {
                        // 
                        validator: (rules, value, callback) => {
                            switch (true) {
                                case value.trim() === '':
                                    callback(new Error(''));
                                    break;
                                default:
                                    callback();
                            }
                        }
                    },
                    {
                    validator: validPhone,
                    trigger: "change"
                }],
            }
        };
    },
    created() {
        //this.getUsersList()
    },
    methods: {
        // 
        regist() {
                // 
            this.$refs.formLogin.validate((valid) => {
                if (!valid) {
                    return
                }
                //  , 
                this.$http.post('/users/add', qs.stringify(this.formLoginData)).then((result) => {
                    this.$router.push('/login')
                    console.log(result.data)
                })

            })
        },
        getUsersList() {
            this.$http.get('/users/list').then((result) => {
                console.log(result.data.data)
            })
        }
    }
};
</script>

<style scoped>
.card {
    margin: 100px auto;
    width: 400px;
}

.success {
    margin-left: 70px;
}
</style>



