<template>
    <div id="login">
        <el-card class="card">
            <div slot="header">
                <!--  -->
                <img src="/static/logo.png">                
                <span></span>
            </div>
        
        <el-form :model="formLoginData" ref="form" label-width="80px" :rules="rules">
            <!-- input -->
            <el-form-item label="" prop="username">
                <el-input v-model="formLoginData.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input type="password" v-model="formLoginData.password"></el-input>
            </el-form-item>
            <el-form-item>
                <!--  -->
                <el-button type="primary" @click="login"></el-button>
                <router-link to="/regist">
                    <el-button type="success" class="success"></el-button>
                </router-link>
            </el-form-item>
        </el-form>
        </el-card>

    </div>
</template>

<script>
export default {
    data(){
        return {
            // 
            formLoginData:{
                username:'',
                password:'',
            },
            // 
            rules:{
                // 
                username:{
                    required: true, message: '', trigger: 'blur'
                },
                // 
                password:{
                    required: true, message: '', trigger: 'blur'
                }
            }
        }
    },
    // 
    methods:{
        login(){
            this.$http.post('/users/login',this.$qs.stringify(this.formLoginData)).then((result)=>{
                if(result.data.error == 0){
                        this.$router.push("/index");
                    }else{
                        this.$message.error('!');
                    }
            })
        }
    }
}
</script>

<style scoped>
    .card{
        margin: 100px auto;
        width: 400px;
    }
    .success{
        margin-left: 70px;
    }
</style>



