<template>
<div id="users">
    <!--  -->
    <el-card :body-style="{ padding: '0px' }">
        <div slot="header">
            <span></span>
        </div>
    <el-table :data="tableData">
        <!--  -->
        <el-table-column prop="username" label="" :width="100"></el-table-column>
        <!--  -->
        <el-table-column prop="email" label=""></el-table-column>
        <!--  -->
        <el-table-column prop="phone" label=""></el-table-column>
        <!--  -->
        <el-table-column prop="" label="">
            <div slot-scope="{row}">
                <!--  -->
                <el-button-group>
                    <el-button type="default" @click="usersEdit(row)" round size="mini"></el-button>
                    <el-button type="default" @click="usersDelete(row.id)" round size="mini"></el-button>
                    <el-button type="default" @click="usersReset(row.id)" round size="mini"></el-button>
                </el-button-group>
            </div>
        </el-table-column>
    </el-table>
    <!-- element-ui -->
    <div style="margin-top:20px">
        <el-pagination :current-page.sync="currentpage" :page-size="pagesize" @current-change="getUsersList" :total="total" background layout="prev,pager,next">
        </el-pagination>
    </div>
    </el-card>
    <!-- dialog -->
    <el-dialog width="400px" :title="dialogTitle" :visible.sync="dialogVisible" @close="dialogClose">
        <el-form label-position="left" :model="formData" ref="formData" :rules="rules" label-width="60px">
            <!--  -->
            <el-form-item label="" prop="username">
                <el-input v-model="formData.username"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="" prop="email">
                <el-input v-model="formData.email"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="" prop="phone">
                <el-input v-model="formData.phone"></el-input>
            </el-form-item>
        </el-form>
        <!--  -->
        <div slot="footer">
            <el-button type="danger" @click="dialogVisible=false"> </el-button>
            <el-button type="primary" @click="menusEdit"> </el-button>
        </div>
    </el-dialog>
</div>
</template>

<script>
// qs
import qs from 'qs'

export default {
    data() {
        // 
        var usernameRegx = (rule, value, callback) => {
            this.$http.get(`/users/isexist?fieldname=username&fieldvalue=${value}`).then((result) => {
                if (result.data.error == 0 || this.oldusername == value) {
                    callback()
                } else {
                    callback(new Error(','));                    
                }
            })
        }
        // 
        var emailRegx = (rule, value, callback) => {
            this.$http.get(`/users/isexist?fieldname=email&fieldvalue=${value}`).then((result) => {
                if (result.data.error == 0 || this.oldemail == value) {
                    callback()
                } else {
                    callback(new Error(','));                    
                }
            })
        }
        // 
        var phoneRegx = (rule, value, callback) => {
            this.$http.get(`/users/isexist?fieldname=phone&fieldvalue=${value}`).then((result) => {
                if (result.data.error == 0 || this.oldphone == value) {
                    callback()
                } else {
                    callback(new Error(','));                    
                }
            })

        }
        return {
            // 
            rules: {
                // 
                username: [
                    {
                        validator: (rules, value, callback) => {
                            switch (true) {
                                case value.trim() === '':
                                    callback(new Error(''));
                                    break;
                                case !/^[a-zA-Z]\w{3,14}$/.test(value):
                                    callback(new Error('415'));
                                    break;
                                default:
                                    callback();
                            }
                        }
                    },{
                        validator: usernameRegx,
                        trigger: 'blur'
                    },
                ],
                // 
                email: [{
                    validator: (rules, value, callback) => {
                        switch (true) {
                            case value.trim() === '':
                                callback(new Error(''));
                                break;
                            case !/^\w+@\w+(\.(com|cn|net|org|edu)){1,2}$/g.test(value):
                                callback(new Error(','));
                                break;
                            default:
                                callback();
                        }
                    }
                },{
                        validator: emailRegx,
                        trigger: 'blur'
                    },
                ],
                //
                phone: [{
                    validator: (rules, value, callback) => {
                        switch (true) {
                            case value.trim() === '':
                                callback(new Error(''));
                                break;
                            case !/^((13|18)(\d{9}))$|^(14[57]\d{8})$|^(17[07]\d{8})$|(^15[0-35-9]\d{8}$)/.test(value):
                                callback(new Error(','));
                                break;
                            default:
                                callback();
                        }
                    }
                },{
                        validator: phoneRegx,
                        trigger: 'blur'
                    },
                ],
            },
            // 
            tableData: [],
            total: 0,
            pagesize: 4,
            currentpage: 1,
            dialogTitle: '',
            dialogVisible: false, //dialog
            oldusername: '',
            oldemail: '',
            oldphone:'',
            formData: {
                username: "",
                email: "",
                phone: "",
            },
        }
    },
    // 
    created() {
        this.getUsersList()
    },
    methods: {
        // 
        getUsersList() {
            this.$http.get(`/users/list?pagesize=${this.pagesize}&currentpage=${this.currentpage}`).then((result) => {
                this.total = result.data.total
                this.tableData = result.data.data
            })
        },
        dialogClose() {
            this.$refs.formData.resetFields() //
        },
        // 
        usersEdit(row) {
            this.dialogVisible = true //dialog
            this.dialogTitle = '' //dialog
            // 
            this.formData.username = row.username
            this.formData.email = row.email
            this.formData.phone = row.phone
            // 
            this.oldemail = row.email
            this.oldphone = row.phone         
            this.oldusername = row.username
            // id
            this.$set(this.formData, 'id', row.id)
        },
        //dialog
        menusEdit() {
            this.$refs['formData'].validate((pass) => {
                if (pass) {
                    this.$http.post('/users/edit', qs.stringify(this.formData)).then((result) => {
                        if(result.error==0){
                            this.$http.get('/users/list').then((result)=>{
                                if(result.error==0){
                                    this.formData = result.data.data
                                }
                            })
                        }
                        this.dialogVisible = false //dialog
                        this.getUsersList() //
                    })
                }else{
                    this.dialogVisible = true
                }
            })

        },
        // 
        usersDelete(id) {
            this.$confirm('?', '', {
                type: 'warning'
            }).then(() => {
                this.$http.get(`/users/delete?id=${id}`).then((result) => {
                    this.getUsersList() //
                })
            }).catch(() => {
                console.log('no')
            })
        },
        // 
        usersReset(id) {
            this.$confirm(':123123 , ?', '', {
                type: 'warning'
            }).then(() => {
                this.$http.get(`/users/resetpassword?id=${id}`).then((result) => {
                    this.getUsersList() //
                })
            }).catch(() => {
                console.log('no')
            })
        }
    }
}
</script>

<style>
    .el-card{
        padding: 20px;
    }
</style>



