<template>
<div id="paperslist">
    <el-card>
        <!--  -->
        <div slot="header">
            <span></span>
            <!--  -->
            <el-button @click="$router.push({name:'papersadd'})" type="primary" size="mini" icon="el-icon-plus"></el-button>
        </div>
        <!--  -->
        <el-table :data="papersList" style="width: 100%">
            <!--  -->
            <el-table-column prop="title" label="">
            </el-table-column>
            <!--  -->
            <el-table-column prop="ctime" label="" :formatter="formatDate">
            </el-table-column>
            <!--  -->
            <el-table-column prop="username" label="">
            </el-table-column>
            <!--  -->
            <el-table-column prop="classname" label="">
            </el-table-column>
            <!--  -->
            <el-table-column label="" width="270">
                <div slot-scope="{row}">
                    <!--  -->
                    <el-button type="text" @click="del(row.id)"></el-button>
                    <!--  -->
                    <router-link :to="{name:'papersedit',params:{id:row.id}}">
                        <el-button type="text"></el-button>
                    </router-link>
                    <!--  -->
                    <router-link :to="{name:'count',params:{paper_id:row.id}}">
                        <el-button type="text"></el-button>
                    </router-link>
                    <!--  -->
                    <router-link :to="{name:'publish',params:{paper_id:row.id}}">
                        <el-button type="text"></el-button>
                    </router-link>
                </div>
            </el-table-column>
        </el-table>
        <!--  -->
        <div style="margin-top:20px">
            <el-pagination :current-page.sync="currentpage" :page-size="pagesize" @current-change="getPapersList" :total="total" background layout="prev,pager,next">
            </el-pagination>
        </div>
    </el-card>

</div>
</template>

<script>
// moment
import moment from 'moment'

export default {
    // 
    data() {
        return {
            papersList: [],
            pagesize: 4,
            currentpage: 1,
            total: 0,
        }
    },
    created() {
        this.getPapersList()
    },
    methods: {
        // moment
        formatDate(row){
            return moment(row.ctime).format('YYYY-MM-DD HH:mm:ss')
        },
        // 
        getPapersList() {
            this.$http.get('/papers/list', {
                params: {
                    pagesize: this.pagesize,
                    currentpage: this.currentpage,
                },
            }).then((result) => {
                this.total = result.data.total                
                this.papersList = result.data.data
            })
        },
        // 
        del(id) {
            this.$confirm('?', '', {
                type: 'warning'
            }).then(() => {
                this.$http.get('/papers/delete', {
                    params: {
                        id
                    },
                }).then((result) => {
                    this.getPapersList()
                })
            }).catch(() => {
                console.log('no')
            })

        },
    }
}
</script>

<style>
.el-button--primary {
    float: right;
}
</style>



